<template>
  <v-dialog v-model="opened" fullscreen>
    <v-card flat class="mx-auto pa-4" max-width="100%">
      <v-card flat class="mx-auto" max-width="50">
        <v-text-field
          max-width="150"
          class="mx-auto"
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card>

    <v-data-table
        :headers="headers"
        :items="items"
        class="elevation-1"
        multi-sort
      >
        <template v-slot:body="{ items }">
          <tbody>
            <tr v-for="item in items" :key="item.name">
              <td>
                <v-chip color="#888" dark>{{ item.name }}</v-chip>
              </td>
              <td>
                <v-simple-checkbox color="#09b" :value="item.plannedRouter" :ripple="false" />
              </td>
              <td>
                <v-simple-checkbox color="#09b" :value="item.plannedSplicingBox" :ripple="false" />
              </td>
              <td>
                <v-simple-checkbox color="#09b" :value="item.plannedSplitter" :ripple="false" />
              </td>
              <td>
                <v-simple-checkbox color="#09b" :value="item.plannedOFTU" :ripple="false" />
              </td>
              <td>
                <v-text-field color="#090" :value="item.installedRouter" />
              </td>
              <td>
                <v-simple-checkbox color="#090" :value="item.installedSplicingBox" :ripple="false" />
              </td>
              <td>
                <v-simple-checkbox color="#090" :value="item.installedSplitter" :ripple="false" />
              </td>
              <td>
                <v-text-field color="#090" :value="item.installedOFTU" />
              </td>
              <td style="width: 120px">
                  <v-icon
                    small
                    color="#090"
                    class="mr-2"
                    @click="editItem(item)"
                  >
                    mdi-pencil
                  </v-icon>
                  <v-icon
                    small
                    color="#900"
                    @click="deleteItem(item)"
                  >
                    mdi-delete
                  </v-icon>
              </td>
            </tr>
          </tbody>
        </template>

        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>{{ address }}</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
            <EditBuildingLevel
              :opened.sync="dialog"
              :index="editedIndex"
              :level.sync="editedItem"
            />
          </v-toolbar>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="initialize">
            Reset
          </v-btn>
        </template>
    </v-data-table>
    </v-card>

    <v-bottom-navigation absolute v-model="value">
      <v-btn
        text
        value="save"
        @click="save"
      >
        <span>SAVE</span>
        <v-icon>mdi-content-save</v-icon>
      </v-btn>

      <!-- <v-btn
        text
        :clicked.sync="save"
      >
        SAVE
      </v-btn> -->
      <Button
        :clicked.sync="hide"
        text="CLOSE"
        color="#333"
      />
    </v-bottom-navigation>
  </v-dialog>
</template>

<script>

// import Button from '@/components/Button.vue'
import EditBuildingLevel from '@/components/EditBuildingLevel.vue'

export default {
  name: 'BuildingLevels',
  components: {
    // Button,
    EditBuildingLevel
  },
  props: ['opened', 'address', 'levels'],
  data () {
    return {
      search: '',
      dialog: false,
      hide: false,
      save: false,
      value: 'save',
      items: [],
      headers: [
        { text: 'Levels', align: 'start', value: 'name' },
        { text: 'Planned Routers', value: 'plannedRouter' },
        { text: 'Planned Splicing Boxes', value: 'plannedSplicingBox' },
        { text: 'Planned Splitters', value: 'plannedSplitter' },
        { text: 'Planned OFTU', value: 'plannedOFTU' },
        { text: 'Installed Routers', value: 'installedRouter' },
        { text: 'Installed Splicing Boxes', value: 'installedSplicingBox' },
        { text: 'Installed Splitters', value: 'installedSplitter' },
        { text: 'Installed OFTU', value: 'installedOFTU' },
        { text: 'Actions', value: 'actions', sortable: false }
      ],
      editedIndex: -1,
      editedItem: {
        name: '',
        plannedRouter: false,
        plannedSplicingBox: false,
        plannedSplitter: false,
        plannedOFTU: false,
        installedRouter: '',
        installedSplicingBox: false,
        installedSplitter: false,
        installedOFTU: ''
      },
      defaultItem: {
        name: '',
        plannedRouter: false,
        plannedSplicingBox: false,
        plannedSplitter: false,
        plannedOFTU: false,
        installedRouter: '',
        installedSplicingBox: false,
        installedSplitter: false,
        installedOFTU: ''
      }
    }
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    hide (val) {
      if (val) this.$emit('update:opened', false)
      this.hide = false
    },
    save (val) {
      if (!val) return
      this.saveBuildingData()
      this.save = false
    },
    levels: {
      deep: true,
      handler (val) {
        console.log('LEVELS:\n', val)
        if (!val) return
        this.items = val.map(level => ({
          name: level.name || '',
          plannedRouter: level.planned ? level.planned.router : false,
          plannedSplicingBox: level.planned ? level.planned.splicingBox : false,
          plannedSplitter: level.planned ? level.planned.splitter : false,
          plannedOFTU: level.planned ? level.planned.oftu : false,
          installedRouter: level.installed ? level.installed.router : '',
          installedSplicingBox: level.installed ? level.installed.splicingBox : false,
          installedSplitter: level.installed ? level.installed.splitter : false,
          installedOFTU: level.installed ? level.installed.oftu : ''
        }))
        console.log(this.items)
        // this.$emit('update:levels', levels)
      }
    },
    items: {
      deep: true,
      handler (val) {
        console.log('ITEMS:\n', val)
        if (!val) return
        const levels = val.map(item => ({
          name: item.name,
          planned: {
            router: item.plannedRouter,
            splicingBox: item.plannedSplicingBox,
            splitter: item.plannedSplitter,
            oftu: item.plannedOFTU
          },
          installed: {
            router: item.installedRouter,
            splicingBox: item.installedSplicingBox,
            splitter: item.installedSplitter,
            oftu: item.installedOFTU
          }
        }))
        console.log(levels)
        this.$emit('update:levels', levels)
      }
    },
    editedItem: {
      deep: true,
      handler (val) {
        console.log('INDEX: ', this.editedIndex)
        console.log('VALUE:\n', val)
      }
    }
  },
  methods: {
    editItem (item) {
      this.editedIndex = this.items.indexOf(item)
      this.editedItem = Object.assign({}, item)
      console.log(this.editedItem)
      this.dialog = true
    },

    deleteItem (item) {
      const index = this.items.indexOf(item)
      this.items.splice(index, 1)
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    saveLevelData () {
      if (this.editedIndex > -1) {
        Object.assign(this.items[this.editedIndex], this.editedItem)
      } else {
        this.items.push(this.editedItem)
      }
      console.log(this.levels)
      this.close()
    },

    saveBuildingData () {
      const building = localStorage.getItemByName('selectedBuilding')
      localStorage.setItemByName('selectedBuilding', Object.assign(building, { levels: this.levels }))
      this._saveBuilding()
    }
  }
}
</script>
