<template>
  <v-dialog v-model="opened" max-width="480px">
    <v-card flat>
      <v-card-text class="text-center">
        <h4>Are you sure you want to delete<br>this building?</h4>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="#09b" text @click="$emit('update:opened', false)">Cancel</v-btn>
        <v-btn color="#09b" text @click="confirmDelete">OK</v-btn>
        <v-spacer></v-spacer>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  name: 'PopupDelete',
  props: ['opened', 'delete'],
  methods: {
    confirmDelete () {
      this.$emit('update:delete', true)
      this.$emit('update:opened', false)
    }
  }
}
</script>
