<template>
  <v-layout row justify-center v-if="ready">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <!-- <template v-slot:activator="{ on }">
        <v-btn color="primary" dark v-on="on">Open Dialog</v-btn>
      </template> -->
      <v-card>
        <v-toolbar dark color="#444">
          <v-btn icon dark @click="$emit('update:opened', false)">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Infrastructure</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn text @click="save">Save</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card flat class="ma-auto">
          <Infrastructure
            v-if="ready"
            :infrastructure.sync="infrastructure"
          />
        </v-card>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>

import Infrastructure from '@/components/tabs/Infrastructure.vue'

export default {
  name: 'SiteServey',
  components: {
    Infrastructure
  },
  props: ['opened'],
  data: () => ({
    buildingData: null,
    ready: false
  }),
  computed: {
    dialog: {
      get () {
        return this.opened
      },
      set (val) {
        this.$emit('update:opened', val)
      }
    },
    infrastructure: {
      get () {
        return this.buildingData ? this.buildingData.infrastructure : {}
      },
      set (val) {
        console.log('INFRASTRUCTURE CHANGED:\n', val)
      }
    }
  },
  watch: {
    opened (val) {
      if (!val) return
      this.buildingData = localStorage.getItemByName('selectedBuilding')
      this.ready = true
    }
  },
  methods: {
    save () {
      localStorage.setItemByName('selectedBuilding', this.buildingData)
      this._saveBuilding()
      this.$emit('update:opened', false)
    }
  }
}
</script>
