<template>
  <v-dialog v-model="dialog" max-width="480px">
    <template v-slot:activator="{ on }">
      <v-btn color="#09b" text class="mb-2" v-on="on"> New Building </v-btn>
    </template>
    <v-card flat>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn icon @click="$emit('update:opened', false)">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-actions>

      <v-card-text>
        <v-text-field v-model="postCode" label="Postal code" />
      </v-card-text>
      <v-card-text>
        <v-text-field v-model="state" label="State" />
      </v-card-text>
      <v-card-text>
        <v-text-field v-model="city" label="City" />
      </v-card-text>
      <v-card-text>
        <v-text-field v-model="street" label="Street name" />
      </v-card-text>
      <v-card-text>
        <v-text-field v-model="number" label="Street number" />
      </v-card-text>
      <v-card-text>
        <v-text-field v-model="buildingName" label="Building Name" />
      </v-card-text>
      <v-select
        :items="buildingTypes"
        label="Type"
        v-model="type"
      />

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="#09b" text @click="saveCallback"> Save </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  name: 'EditListItem',

  props: ['opened', 'editedItem', 'save'],

  computed: {
    dialog: {
      get () {
        return this.opened
      },
      set (val) {
        this.$emit('update:opened', val)
      }
    },
    postCode: {
      get () {
        return this.editedItem.postCode
      },
      set (val) {
        this.$emit('update:editedItem', Object.assign(this.editedItem, { postCode: val }))
      }
    },
    state: {
      get () {
        return this.editedItem.state
      },
      set (val) {
        this.$emit('update:editedItem', Object.assign(this.editedItem, { state: val }))
      }
    },
    city: {
      get () {
        return this.editedItem.city
      },
      set (val) {
        this.$emit('update:editedItem', Object.assign(this.editedItem, { city: val }))
      }
    },
    street: {
      get () {
        return this.editedItem.street
      },
      set (val) {
        this.$emit('update:editedItem', Object.assign(this.editedItem, { street: val }))
      }
    },
    number: {
      get () {
        return this.editedItem.number
      },
      set (val) {
        this.$emit('update:editedItem', Object.assign(this.editedItem, { number: val }))
      }
    },
    buildingName: {
      get () {
        return this.editedItem.buildingName
      },
      set (val) {
        this.$emit('update:editedItem', Object.assign(this.editedItem, { buildingName: val }))
      }
    },
    type: {
      get () {
        return this.editedItem.type
      },
      set (val) {
        this.$emit('update:editedItem', Object.assign(this.editedItem, { type: val }))
      }
    }
  },
  methods: {
    saveCallback () {
      this.$emit('update:save', true)
      this.$emit('update:opened', false)
    }
  }
}
</script>
