<template>
  <v-card v-if="ready || search" class="mt-12">
    <v-select
      v-if="!search"
      :items="types"
      label="Type"
      v-model="type"
      outlined
      class="mx-4"
    ></v-select>

    <v-card-text class="mx-auto px-auto">
      <InputWithAutocomplete
        :address.sync="buildingDescription.address"
        :coordinates.sync="buildingDescription.coordinates"
        :properties.sync="buildingDescription.addressComponents"
        :search="search"
      />
    </v-card-text>

    <v-card-text class="text-center">
      <h4>Address components</h4>
    </v-card-text>

    <AddressDetails
      v-if="addressComponents"
      :number="addressComponents.number"
      :street="addressComponents.street"
      :city="addressComponents.city"
      :state="addressComponents.state"
      :admin="addressComponents.admin"
      :postCode="addressComponents.postCode"
    />
    <v-card-actions>
      <!-- <Button
        v-if="newMarkerButton"
        :clicked.sync="newMarker"
        text="Add new address"
        color="buttons"
      /> -->

      <v-btn
        text
        v-if="newMarkerButton"
        @click="createNewBuilding"
      >
        Add this address
      </v-btn>
      <!-- <Button
        v-if="!search"
        :clicked.sync="saveChanges"
        text="Save changes"
        color="#09b"
      /> -->
      <!-- <v-spacer /> -->
      <!-- <Button
        v-if="!search"
        :clicked.sync="removeMarker"
        text="Remove building"
        color="#900"
      /> -->
    </v-card-actions>
  </v-card>
</template>

<style>

.input-field-simple {
  outline: none;
  width: 100%;
  padding: 8px 16px;
  border: solid 1px #bbb;
  border-radius: 8px;
}
.input-field-simple:focus {
  outline: none;
  border: solid 1px #09b;
}

.text-left {
  text-align: left !important;
}
.text-right {
  text-align: right !important;
}

.vs__clear {
  display: none!important;
}

.vs--searchable .vs__dropdown-toggle {
    cursor: pointer!important;
    padding: 16px!important;
    background-color: #09b!important;
    color: #fff!important;
}

.vs--single .vs__selected {
    background-color: transparent;
    border-color: transparent;
    color: #fff;
}

#vs1__listbox {
  line-height:180%;
}

</style>

<script>

import InputWithAutocomplete from '@/components/InputWithAutocomplete.vue'
import AddressDetails from '@/components/AddressDetails.vue'
// import Button from '@/components/Button.vue'

export default {
  name: 'AddressInfo',

  components: {
    // Button,
    InputWithAutocomplete,
    AddressDetails
  },

  props: ['buildingId', 'descriptionChanged', 'search'],

  data () {
    return {
      newBuilding: require('@/config/newBuilding.js').default,
      ready: false,
      removeMarker: false,
      newMarker: false,
      saveChanges: false,
      types: localStorage.buildingTypes,
      buildingDescription: null,
      autocomplete: null,
      message: '',
      first: '',
      second: '',
      third: '',
      addressDescriptionVisible: false
    }
  },

  computed: {
    newMarkerButton () {
      return !this.buildingId && !!this.buildingDescription.address && !!this.buildingDescription.coordinates
    },
    type: {
      get () {
        return this.buildingId && this.buildingDescription ? this.buildingDescription.type : null
      },
      set (val) {
        if (!val) return
        this.buildingDescription.type = val
        localStorage.updateMarkerType(val)
        this.$root.$map[this.buildingId].type = val
        Object.assign(this.buildingDescription, { type: val })
      }
    },
    addressComponents () {
      return this.buildingDescription ? this.buildingDescription.addressComponents
        : { address: '', addressComponents: {}, coordinates: [] }
    }
  },

  watch: {
    addressComponents: {
      deep: true,
      handler (val) {
        if (this.buildingDescription && this.buildingDescription.addressComponents) {
          Object.assign(this.buildingDescription.addressComponents, val)
        }
      }
    },
    buildingDescription: {
      deep: true,
      handler (val) {
        if (!val) { return }
        localStorage.setItemByName('selectedBuilding', val)
        this.$emit('update:descriptionChanged', true)
      }
    },
    buildingId: {
      immediate: true,
      handler (newVal, oldVal) {
        this.ready = false
        if (!newVal || !this.buildingId) {
          this.buildingDescription = this.newBuilding
          return
        }
        this.buildingDescription = localStorage.getItemByName('selectedBuilding')
        this.ready = true
      }
    },

    newMarker (val) {
      if (!val) return
      if (!this.buildingDescription) return console.warn('Not available!')
      this.createNewBuilding()
        .then(building => {
          Object.assign(this.$parent, {
            selectedMarkerId: building._id,
            selectedMarker: building
          })
          console.log(this.$parent)

          const placeMarker = this.$root.$map.placeMarker.bind(this.$root.$map)

          this.$root.$map[building._id] = placeMarker({
            id: building._id,
            address: building.address,
            coordinates: building.typecoordinates,
            type: building.type
          })

          console.log(this.$root.$map[building._id])

          this.$root.$map.selectMarkerById(building._id)
          // setTimeout(this.$root.$map.setMarkerActive.bind(this.$root.$map))
          this.newMarker = false
        })
    },

    removeMarker (val) {
      if (!val || !this.buildingId) return
      this.$parent.map.removeMarker(this.buildingId)
      localStorage.removeBuilding(this.buildingId)
      this.removeMarker = false
      this.$emit('update:buildingId', undefined)
    },

    saveChanges (val) {
      if (val) this._saveBuilding()
      this.saveChanges = false
    }
  },

  methods: {

    async createNewBuilding () {
      const building = this.newBuilding
      Object.assign(building, this.buildingDescription, { type: this.buildingTypes.default })
      building._id = await this._createNewBuilding(building)
      if (!building._id) return
      const { address, addressComponents, buildingName, coordinates, distanceFromFootprint, type, _id } = building
      localStorage.setItemByName(building._id, {
        address,
        addressComponents,
        buildingName,
        coordinates,
        distanceFromFootprint,
        type,
        _id
      })

      const marker = this.$root.$map.placeMarker({ address, coordinates, type, id: _id })
      console.log(marker)
      this.$root.$map.selectedMarkerId = _id
      console.log(this.$root.$map.selectedMarkerId)
      return building
    }
  },

  beforeMount () {
    this.buildingDescription = this.buildingId ? localStorage.getItemByName(this.buildingId) : {}
  }
}
</script>
