export default async (buildingData) => {
  console.log('POST:\n', buildingData)
  const response = await (await fetch(process.env.VUE_APP_BUILDINGS_API_ENDPOINT, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: process.env.VUE_APP_BUILDINGS_API_KEY
    },
    body: JSON.stringify(buildingData)
  })).json()

  if (response.error) {
    console.warn(response.error)
    return null
  }

  console.log('RESPONSE: ', response.data)

  localStorage.addNewBuilding(response.data, Object.assign(buildingData, { _id: response.data }))
  return response.data
}
