<template>
  <v-container>
    <v-card-text>
      <h4>Infrastructure Type:</h4>
      <v-text-field
        v-model="type"
      />
    </v-card-text>
    <v-simple-table>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left">Name</th>
            <th class="text-left">exist and usable</th>
            <th class="text-left">planned</th>
            <th class="text-left">installed</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>
              <h4>Lead-In</h4>
            </td>
            <td></td>
            <td>
              <v-checkbox
                v-model="leadInPlanned"
              ></v-checkbox>
            </td>
            <td>
              <v-checkbox
                v-model="leadInInstalled"
              ></v-checkbox>
            </td>
          </tr>

          <tr>
            <td>
              <h4>Backbone</h4>
            </td>
            <td></td>
            <td>
              <v-checkbox
                v-model="backbonePlanned"
              ></v-checkbox>
            </td>
            <td>
              <v-checkbox
                v-model="backboneInstalled"
              ></v-checkbox>
            </td>
          </tr>

          <tr>
            <td>
              <h4>Customer Access cabling</h4>
            </td>
            <td></td>
            <td>
              <v-checkbox
                v-model="customerAccessCablingPlanned"
              ></v-checkbox>
            </td>
            <td>
              <v-checkbox
                v-model="customerAccessCablingInstalled"
              ></v-checkbox>
            </td>
          </tr>

          <tr>
            <td>
              <h4>GPO in MDF</h4>
            </td>
            <td>
              <v-checkbox
                v-model="GPOinMDFExist"
              ></v-checkbox>
            </td>
            <td>
              <v-checkbox
                v-model="GPOinMDFPlanned"
              ></v-checkbox>
            </td>
            <td>
              <v-checkbox
                v-model="GPOinMDFInstalled"
              ></v-checkbox>
            </td>
          </tr>

          <tr>
            <td>
              <h4>UPS in MDF</h4>
            </td>
            <td></td>
            <td>
              <v-checkbox
                v-model="UPSinMDFPlanned"
              ></v-checkbox>
            </td>
            <td>
              <v-checkbox
                v-model="UPSinMDFInstalled"
              ></v-checkbox>
            </td>
          </tr>

          <tr>
            <td>
              <h4>GPO in risers</h4>
            </td>
            <td>
              <v-checkbox
                v-model="GPOinRisersExist"
              ></v-checkbox>
            </td>
            <td>
              <v-checkbox
                v-model="GPOinRisersPlanned"
              ></v-checkbox>
            </td>
            <td>
              <v-checkbox
                v-model="GPOinRisersInstalled"
              ></v-checkbox>
            </td>
          </tr>

          <tr>
            <td>
              <h4>UPS in risers</h4>
            </td>
            <td></td>
            <td>
              <v-checkbox
                v-model="UPSinRisersPlanned"
              ></v-checkbox>
            </td>
            <td>
              <v-checkbox
                v-model="UPSinRisersInstalled"
              ></v-checkbox>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
  </v-container>
</template>

<script>

export default {
  name: 'Infrastructure',
  props: ['infrastructure'],
  computed: {
    type: {
      get () {
        return this.infrastructure.type || ''
      },
      set (val) {
        this.$emit('update:infrastructure', Object.assign(this.infrastructure, { type: val }))
      }
    },
    /* ---------------- leadIn --------------- */
    leadInPlanned: {
      get () {
        return this.infrastructure.leadIn ? this.infrastructure.leadIn.planned : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('leadIn', 'planned', val))
      }
    },
    leadInInstalled: {
      get () {
        return this.infrastructure.leadIn ? this.infrastructure.leadIn.installed : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('leadIn', 'installed', val))
      }
    },
    /* ---------------- backbone --------------- */
    backbonePlanned: {
      get () {
        return this.infrastructure.backbone ? this.infrastructure.backbone.planned : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('backbone', 'planned', val))
      }
    },
    backboneInstalled: {
      get () {
        return this.infrastructure.backbone ? this.infrastructure.backbone.installed : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('backbone', 'installed', val))
      }
    },
    /* ---------------- customerAccessCabling --------------- */
    customerAccessCablingPlanned: {
      get () {
        return this.infrastructure.customerAccessCabling ? this.infrastructure.customerAccessCabling.planned : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('customerAccessCabling', 'planned', val))
      }
    },
    customerAccessCablingInstalled: {
      get () {
        return this.infrastructure.customerAccessCabling ? this.infrastructure.customerAccessCabling.installed : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('customerAccessCabling', 'installed', val))
      }
    },
    /* ---------------- GPOinMDF --------------- */
    GPOinMDFExist: {
      get () {
        return this.infrastructure.gPOinMDF ? this.infrastructure.gPOinMDF.exist : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('gPOinMDF', 'exist', val))
      }
    },
    GPOinMDFPlanned: {
      get () {
        return this.infrastructure.gPOinMDF ? this.infrastructure.gPOinMDF.planned : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('gPOinMDF', 'planned', val))
      }
    },
    GPOinMDFInstalled: {
      get () {
        return this.infrastructure.gPOinMDF ? this.infrastructure.gPOinMDF.installed : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('gPOinMDF', 'installed', val))
      }
    },
    /* ---------------- UPSinMDF --------------- */
    UPSinMDFPlanned: {
      get () {
        return this.infrastructure.uPSinMD ? this.infrastructure.uPSinMDF.planned : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('uPSinMDF', 'planned', val))
      }
    },
    UPSinMDFInstalled: {
      get () {
        return this.infrastructure.uPSinMD ? this.infrastructure.uPSinMDF.installed : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('uPSinMDF', 'installed', val))
      }
    },
    /* ---------------- GPOinRisers --------------- */
    GPOinRisersExist: {
      get () {
        return this.infrastructure.gPOinRisers ? this.infrastructure.gPOinRisers.exist : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('gPOinRisers', 'exist', val))
      }
    },
    GPOinRisersPlanned: {
      get () {
        return this.infrastructure.gPOinRisers ? this.infrastructure.gPOinRisers.planned : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('gPOinRisers', 'planned', val))
      }
    },
    GPOinRisersInstalled: {
      get () {
        return this.infrastructure.gPOinRisers ? this.infrastructure.gPOinRisers.installed : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('gPOinRisers', 'installed', val))
      }
    },
    /* ---------------- UPSinRisers --------------- */
    UPSinRisersPlanned: {
      get () {
        return this.infrastructure.uPSinRisers ? this.infrastructure.uPSinRisers.planned : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('uPSinRisers', 'planned', val))
      }
    },
    UPSinRisersInstalled: {
      get () {
        return this.infrastructure.uPSinRisers ? this.infrastructure.uPSinRisers.installed : false
      },
      set (val) {
        this.$emit('update:infrastructure', this.setValue('uPSinRisers', 'installed', val))
      }
    }
  },
  methods: {
    setValue (propName, option, val) {
      const object = JSON.parse(JSON.stringify(this.infrastructure))
      object[propName][option] = val
      return object
    }
  }
}
</script>
