<template>
  <v-dialog v-model="dialog" max-width="600px">
    <template v-slot:activator="{ on }">
      <v-btn
        color="#333"
        text
        class="mb-2"
        v-on="on"
      >
        Add level
      </v-btn>
    </template>
    <v-card flat>
      <!-- <v-card-title>
        <span class="headline">
          Buildings API
        </span>
      </v-card-title> -->
      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-text-field v-model="name" label="Level" />
            </v-col>
            <v-col cols="6">
              <v-checkbox
                v-model="plannedRouter"
                label="Level Router is planned"
                color="#09b"
              ></v-checkbox>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="installedRouter"
                label="Router installed"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
               <v-checkbox
                v-model="plannedSplicingBox"
                label="Splicing Box is planned"
                color="#09b"
              ></v-checkbox>
            </v-col>
            <v-col cols="6">
              <v-checkbox
                v-model="installedSplicingBox"
                label="Splicing Box installed"
                color="#090"
              ></v-checkbox>
            </v-col>
            <v-col cols="6">
              <v-checkbox
                v-model="plannedSplitter"
                label="Splitter is planned"
                color="#09b"
              ></v-checkbox>
            </v-col>
            <v-col cols="6">
              <v-checkbox
                v-model="installedSplitter"
                label="Splitter installed"
                color="#090"
              ></v-checkbox>
            </v-col>
            <v-col cols="6">
              <v-checkbox
                v-model="plannedOFTU"
                label="OFTU is planned"
                color="#09b"
              ></v-checkbox>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="installedOFTU"
                label="OFTU installed (serial number)"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="#333" text @click="$emit('update:opened', false)">
          Close
        </v-btn>
        <!-- <v-btn color="#333" text @click="save">
          Save
        </v-btn> -->
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  name: 'EditBuildingLevel',
  props: ['opened', 'level'],
  computed: {
    dialog: {
      get () {
        return this.opened
      },
      set (val) {
        this.$emit('update:opened', val)
      }
    },
    name: {
      get () {
        return this.level.name
      },
      set (val) {
        this.$emit('update:level', Object.assign(this.level, { name: val }))
      }
    },
    plannedRouter: {
      get () {
        return this.level.plannedRouter
      },
      set (val) {
        this.$emit('update:level', Object.assign(this.level, { plannedRouter: val }))
      }
    },
    installedRouter: {
      get () {
        return this.level.installedRouter
      },
      set (val) {
        this.$emit('update:level', Object.assign(this.level, { installedRouter: val }))
      }
    },
    plannedSplicingBox: {
      get () {
        return this.level.plannedSplicingBox
      },
      set (val) {
        this.$emit('update:level', Object.assign(this.level, { plannedSplicingBox: val }))
      }
    },
    installedSplicingBox: {
      get () {
        return this.level.installedSplicingBox
      },
      set (val) {
        this.$emit('update:level', Object.assign(this.level, { installedSplicingBox: val }))
      }
    },
    plannedSplitter: {
      get () {
        return this.level.plannedSplitter
      },
      set (val) {
        this.$emit('update:level', Object.assign(this.level, { plannedSplitter: val }))
      }
    },
    installedSplitter: {
      get () {
        return this.level.installedSplitter
      },
      set (val) {
        this.$emit('update:level', Object.assign(this.level, { installedSplitter: val }))
      }
    },
    plannedOFTU: {
      get () {
        return this.level.plannedOFTU
      },
      set (val) {
        this.$emit('update:level', Object.assign(this.level, { plannedOFTU: val }))
      }
    },
    installedOFTU: {
      get () {
        return this.level.installedOFTU
      },
      set (val) {
        this.$emit('update:level', Object.assign(this.level, { installedOFTU: val }))
      }
    }
  },
  methods: {
    //
  }
}
</script>
