<template>
  <v-container fluid>
    <v-card flat class="transparent">
      <v-data-table
        :headers="headers"
        :items="items"
        item-key="_id"
        class="elevation-1 pa-8"
        :single-expand="true"
        :expanded.sync="expanded"
        show-expand
        dence
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title><h5>List Of Buildings</h5></v-toolbar-title>
            <v-spacer></v-spacer>
            <!-- <template v-slot:activator="{ on }">
              <v-btn color="#09b" text class="mb-2" v-on="on"> New Building </v-btn>
            </template> -->

            <EditListItem
              :opened.sync="dialog"
              :editedItem.sync="editedItem"
              :save.sync="saveBuilding"
            />

            <PopupDelete :opened.sync="dialogDelete" :delete.sync="deleteConfirmed" />

          </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon class="mr-4" :color="getColor(item.type)">{{ getIcon(item.type) }}</v-icon>
          <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
          <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
        </template>

        <template v-slot:body.append>
          <tr>
            <td width="140">
              <v-text-field
                v-model="state"
                type="string"
                outlined
                hide-details
                append-icon="mdi-magnify"
              ></v-text-field>
            </td>
            <td width="120">
              <v-text-field
                v-model="postcode"
                type="string"
                outlined
                hide-details
                append-icon="mdi-magnify"
              ></v-text-field>
            </td>
            <td width="240">
              <v-text-field
                v-model="city"
                type="string"
                outlined
                hide-details
                append-icon="mdi-magnify"
              ></v-text-field>
            </td>
            <td width="240">
              <v-text-field
                v-model="street"
                type="string"
                outlined
                hide-details
                append-icon="mdi-magnify"
              ></v-text-field>
            </td>
          </tr>
        </template>
        <template v-slot:expanded-item="{ headers, item }">
          <td :colspan="headers.length" class="pa-10">
            <v-icon :color="getColor(item.type)">
              {{ getIcon(item.type) }}
            </v-icon><span>{{ item.type }}</span>
            <p>{{ item.buildingName }}</p>
          </td>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>

import PopupDelete from '@/components/PopupDelete.vue'
import EditListItem from '@/components/EditListItem.vue'

export default {

  name: 'ListOfBuildings',

  components: {
    PopupDelete,
    EditListItem
  },

  data: () => ({
    dialog: false,
    dialogDelete: false,
    deleteConfirmed: false,
    saveBuilding: false,
    expanded: [],
    items: localStorage.getAllBuildings()
      .map(item => Object.assign(item.addressComponents, {
        _id: item._id,
        buildingName: item.buildingName,
        type: item.type
      })),
    search: '',
    postcode: '',
    state: '',
    city: '',
    street: '',
    editedIndex: -1,
    editedItem: {},
    defaultItem: { city: '', postCode: '', state: '', street: '', number: 1 }
  }),

  computed: {
    headers () {
      return [
        {
          text: 'State',
          align: 'start',
          sortable: true,
          value: 'state',
          filter: value => {
            if (!this.state) return true
            return value.toLowerCase().indexOf(this.state.toLowerCase()) !== -1
          }
        },
        {
          text: 'Postal Code',
          value: 'postCode',
          filter: value => {
            if (!this.postcode) return true
            return value === this.postcode
          }
        },
        {
          text: 'City',
          value: 'city',
          filter: value => {
            if (!this.city) return true
            return value.toLowerCase().indexOf(this.city.toLowerCase()) !== -1
          }
        },
        {
          text: 'Street Name',
          value: 'street',
          filter: value => {
            if (!this.street) return true
            return value.toLowerCase().indexOf(this.street.toLowerCase()) !== -1
          }
        },
        {
          text: 'Street Number',
          value: 'number'
        },
        {
          text: 'Connection',
          value: 'connect',
          sortable: false
        },
        {
          text: 'Actions',
          value: 'actions',
          sortable: false
        },
        { text: '', value: 'data-table-expand' }
      ]
    }
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    deleteConfirmed (val) {
      val && this.deleteBuilding()
    },
    saveBuilding (val) {
      console.log('SAVE: ', val)
      if (val) this.saveNewBuilding()
    }
  },

  methods: {
    getIcon (type) {
      const index = this.buildingTypes.indexOf(type)
      if (index === -1) {
        console.warn('Invalid Building Type: ', type)
        return this.typeIcons.slice(-1)[0]
      }
      return this.typeIcons[index]
    },
    getColor (type) {
      const index = this.buildingTypes.indexOf(type)
      if (index === -1) {
        console.warn('Invalid Building Type: ', type)
        return this.typeColors.slice(-1)[0]
      }
      return this.typeColors[index]
    },
    editItem (item) {
      this.editedIndex = this.items.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      console.log(item)
      this.editedIndex = this.items.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteBuilding () {
      console.log(this.items[this.editedIndex]._id)
      this._deleteBuilding(this.items[this.editedIndex]._id)
      this.items.splice(this.editedIndex, 1)
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
      this.dialogDelete = false
      this.deleteConfirmed = false
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    // closeDelete () {
    //   this.dialogDelete = false
    //   this.$nextTick(() => {
    //     this.editedItem = Object.assign({}, this.defaultItem)
    //     this.editedIndex = -1
    //   })
    // },

    async saveNewBuilding () {
      if (this.editedIndex !== -1) {
        Object.assign(this.items[this.editedIndex], this.editedItem)
      } else {
        const { number, street, city, state, postCode, buildingName, type } = this.editedItem
        const newBuilding = require('@/config/newBuilding').default
        Object.assign(newBuilding.addressComponents, { number, street, city, state, postCode })
        Object.assign(newBuilding, {
          buildingName,
          type,
          address: `${number} ${street}, ${city} ${state} ${postCode}, Australia`
        })
        const id = await this._createNewBuilding(newBuilding)
        localStorage.setItemByName('selectedBuilding', Object.assign(newBuilding, { _id: id }))
        this.items.push(Object.assign(this.editedItem, { _id: id }))
        localStorage.setItemByName(id, newBuilding)
      }
      // this.dialog = false
    }
  }
}
</script>

