<template>
  <v-app>
    <v-app-bar
      absolute
      color="white"
      elevate-on-scroll
    >
      <!-- <v-app-bar-nav-icon></v-app-bar-nav-icon> -->

      <v-toolbar-title>
        <h5>Buildings API</h5>
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-tooltip bottom color="#090" v-if="selectedBuilding">
        <template v-slot:activator="{ on }">
          <v-btn
            icon
            v-on="on"
            @click="_saveBuilding"
          >
            <v-icon>mdi-content-save</v-icon>
          </v-btn>
        </template>
        <span>Save current building data</span>
      </v-tooltip>

      <v-tooltip bottom color="#900" v-if="selectedBuilding">
        <template v-slot:activator="{ on }">
          <v-btn
            icon
            v-on="on"
            @click="confirmDeleteVisibility = true"
          >
            <v-icon>mdi-trash-can</v-icon>
          </v-btn>
        </template>
        <span>Remove current building forever</span>
      </v-tooltip>
    </v-app-bar>

    <v-main id="dgtek-markers">
      <Buildings
        v-show="value === 'home'"
        :buildingSelected.sync="selectedBuilding"
      />
      <ListOfBuildings v-if="value === 'list'" />
      <SiteSurvey :opened.sync="infrastructure" />
      <BuildingLevels
        :opened.sync="levels"
        :address="address"
        :levels="buildingLevels"
      />
    </v-main>
    <!--            BOTTOM NAVIGATION             -->
    <v-bottom-navigation absolute v-model="value" color="#900">
      <v-btn value="home">
        <span>Home</span>
        <v-icon>mdi-map</v-icon>
      </v-btn>
      <v-btn value="list">
        <span>List of buildings</span>
        <v-icon>mdi-view-list</v-icon>
      </v-btn>
      <v-btn value="infrastructure" v-if="selectedBuilding">
        <span>Infrastructure</span>
        <v-icon>mdi-pencil-ruler</v-icon>
      </v-btn>
      <v-btn value="levels" v-if="selectedBuilding">
        <span>Building Levels</span>
        <v-icon>mdi-home-city-outline</v-icon>
      </v-btn>
    </v-bottom-navigation>

    <ConfirmDelete
      :visibility.sync="confirmDeleteVisibility"
      :response.sync="removingConfirmed"
    />
  </v-app>
</template>

<script>

import Buildings from '@/components/Buildings.vue'
import ListOfBuildings from '@/components/ListOfBuildings.vue'
import SiteSurvey from '@/components/SiteSurvey.vue'
import BuildingLevels from '@/components/BuildingLevels.vue'
import ConfirmDelete from '@/components/work-with-files/ConfirmDelete.vue'

export default {
  name: 'App',

  components: {
    Buildings,
    ListOfBuildings,
    SiteSurvey,
    BuildingLevels,
    ConfirmDelete
  },

  data: () => ({
    ready: false,
    address: '',
    buildingLevels: null,
    value: 'home',
    selectedBuilding: false,
    selectedBuildingId: null,
    confirmDeleteVisibility: false,
    removingConfirmed: false
    // siteSurvey: false
  }),

  computed: {
    infrastructure: {
      get () {
        return this.value === 'infrastructure'
      },
      set (val) {
        this.value = val ? 'infrastructure' : 'home'
      }
    },

    levels () {
      return this.value === 'levels'
    }
  },

  watch: {
    levels (val) {
      if (!this.selectedBuilding) return
      const buildingData = localStorage.getItemByName('selectedBuilding')
      this.address = buildingData.address
      this.buildingLevels = buildingData.levels
    },

    removingConfirmed (val) {
      if (val) {
        this._deleteBuilding()
        this.$root.$map.removeMarker()
        this.$emit('reset-selected-marker')
        this.removingConfirmed = false
      }
    }
  },

  methods: {
    // test () {
    //   console.log(this.$root.$map)
    // }
  },

  beforeMount () {
    this._getBuildingsList()
      .then(() => {
        this.ready = true
        window.dispatchEvent(new Event('data-ready'))
      })
  },
  mounted () {
    // setTimeout(this.test.bind(this), 1000)
  }
}
</script>
