export default async (formData) => {
  const building = localStorage.getItemByName('selectedBuilding')
  const response = await (await fetch(`${process.env.VUE_APP_BUILDINGS_API_ENDPOINT}/upload/design/${building._id}`, {
    method: 'POST',
    headers: {
      Authorization: process.env.VUE_APP_BUILDINGS_API_KEY
    },
    body: JSON.stringify(formData)
  })).json()

  console.log('UPLOAD CURRENT BUILDING DESIGN:\n', response)

  if (response.error) {
    console.warn(response.error)
    return null
  }
  building.files.design.link = response.data
  localStorage.setBuilingsListItem(building._id, building)

  return response.data
}
