import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import './config/Storage.js'

Vue.config.productionTip = false

const { types, colors, icons, defaultType } = require('@/config/types').default;

[Vue, Storage].forEach(constructor => Object.assign(constructor.prototype, {
  buildingTypes: Object.assign([], types, {
    default: defaultType
  }),
  typeColors: Object.assign([], colors),
  typeIcons: Object.assign([], icons)
}))

Vue.prototype._getBuildingsList = require('@/helpers/getBuildingsList').default
Vue.prototype._getBuildingDetails = require('@/helpers/getBuildingDetails').default
Vue.prototype._createNewBuilding = require('@/helpers/createNewBuilding').default
Vue.prototype._saveBuilding = require('@/helpers/saveBuilding').default
Vue.prototype._deleteBuilding = require('@/helpers/deleteBuilding').default
Vue.prototype._uploadBuildingDesign = require('@/helpers/uploadBuildingDesign').default

Vue.prototype.$role = location.pathname.slice(1)

new Vue({
  vuetify,
  render: h => h(App)
}).$mount('#app')
